@media only screen and (max-width : 756px) {
  body {
    text-align: center;
    background-size: 147%;
    background-position: 98% 55vh;
    h1 {
      font-size:  8.5vw; }
    p {
      font-size:  5.16667vw; }
    .text-block {
        margin-top: 22.16667vw;
        padding-top: 0;
        padding-bottom: get-vw(300px); }

    .time {
       left: 50%;
       transform: translateX(-50%);
       font-size: 7.8125vw;
       margin-top: get-vw(100px);
       padding: get-vw(100px)0;
       span {
         font-size: get-vw(70px); }
       &-wrapper {
           padding: 0 2.30208vw; } } } }
