//base
@import "base/normalise";
@import "base/fonts";
@import "base/vars";

body {
  font-family: 'Roboto', sans-serif;
  background-image: url(../img/bg.png);
  background-size: auto 100vh;
  background-position: 100% 0%;
  background-repeat: no-repeat;
  position: relative;

  .container {
    max-width: 1500px;
    margin: 0 auto; }
  .text-block {
    padding-top: get-vw(200px); }

  h1 {
    font-size: get-vw(72px);
    color: #2964cf;
    padding-top: get-vw(50px);
    font-weight: 700;
    margin-bottom: get-vw(15px); }
  p {
    color: #1e3b45;
    font-size: get-vw(24px);
    font-weight: 300; }

  .time {
    font-size: get-vw(72px);
    color: #fff;
    background: #3962e2;
    border-radius: 3px;
    position: absolute;
    padding: get-vw(25px) 19% get-vw(25px) 11%;
    left: 0;
    text-transform: uppercase;
    span {
      font-size: get-vw(14px);
      font-weight: 400;
      display: block;
      padding-left: get-vw(15px);
      margin-bottom: get-vw(15px); }

    &-wrapper {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      font-weight: 700;

      div:not(:first-child) {
        padding-left: get-vw(30px); } } }


  .bottom {
    &-content {
      display: flex;
      justify-content: space-between;
      padding-top: 20%; }
    &-left {
      h2 {
        position: relative;
        margin-top:  get-vw(45px);
        &:before {
          content: '';
          position: absolute;
          width: get-vw(28px);
          height: get-vw(36px);
          background-color: #f9cd56;
          top: get-vw(3px);
          left: get-vw(-55px);
          transform: skewX(20deg); } } }
    &-right {
      h2 {
        text-align: right; } } } }



@import "media/media";




