@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&subset=cyrillic,cyrillic-ext");
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: border-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

body {
  font-family: 'Roboto', sans-serif;
  background-image: url(../img/bg.png);
  background-size: auto 100vh;
  background-position: 100% 0%;
  background-repeat: no-repeat;
  position: relative; }
  body .container {
    max-width: 1500px;
    margin: 0 auto; }
  body .text-block {
    padding-top: 10.41667vw; }
  body h1 {
    font-size: 3.75vw;
    color: #2964cf;
    padding-top: 2.60417vw;
    font-weight: 700;
    margin-bottom: 0.78125vw; }
  body p {
    color: #1e3b45;
    font-size: 1.25vw;
    font-weight: 300; }
  body .time {
    font-size: 3.75vw;
    color: #fff;
    background: #3962e2;
    border-radius: 3px;
    position: absolute;
    padding: 1.30208vw 19% 1.30208vw 11%;
    left: 0;
    text-transform: uppercase; }
    body .time span {
      font-size: 0.72917vw;
      font-weight: 400;
      display: block;
      padding-left: 0.78125vw;
      margin-bottom: 0.78125vw; }
    body .time-wrapper {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      font-weight: 700; }
      body .time-wrapper div:not(:first-child) {
        padding-left: 1.5625vw; }
  body .bottom-content {
    display: flex;
    justify-content: space-between;
    padding-top: 20%; }
  body .bottom-left h2 {
    position: relative;
    margin-top: 2.34375vw; }
    body .bottom-left h2:before {
      content: '';
      position: absolute;
      width: 1.45833vw;
      height: 1.875vw;
      background-color: #f9cd56;
      top: 0.15625vw;
      left: -2.86458vw;
      transform: skewX(20deg); }
  body .bottom-right h2 {
    text-align: right; }

@media only screen and (max-width: 756px) {
  body {
    text-align: center;
    background-size: 147%;
    background-position: 98% 55vh; }
    body h1 {
      font-size: 8.5vw; }
    body p {
      font-size: 5.16667vw; }
    body .text-block {
      margin-top: 22.16667vw;
      padding-top: 0;
      padding-bottom: 15.625vw; }
    body .time {
      left: 50%;
      transform: translateX(-50%);
      font-size: 7.8125vw;
      margin-top: 5.20833vw;
      padding: 5.20833vw 0; }
      body .time span {
        font-size: 3.64583vw; }
      body .time-wrapper {
        padding: 0 2.30208vw; } }
